<template>
  <div>
    <div class="container-fluid">
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <div class="row d-flex justify-content-center">
        <div class="col-md-6 content-center card-body">
          <div class="title-box-d">
            <h3 class="title-d font-weight-bold">CREATE YOUR ACCOUNT</h3>
          </div>

          <div>
            <form class="form-a" @submit.prevent="register" novalidate="true">
              <div class="row">
                <div v-if="errors.length">
                  <b>Please correct the following error(s):</b>
                  <ul style="color:red">
                    <li v-for="error in errors">{{ error }}</li>
                  </ul>
                </div>
                <div class="col-md-8 mb-3 block">
                  <div class="form-group">
                    <label for="inputName">Email</label>
                    <input
                      class="form-control form-control-lg form-control-a"
                      id="inputName"
                      type="email"
                      placeholder="Name"
                      name="email"
                      v-model="email"
                    />
                  </div>
                  <div v-if="errors.length">{{error}}</div>
                </div>
                <div class="col-md-8 mb-3 block">
                  <div class="form-group">
                    <label for="inputName">Password</label>
                    <input
                      class="form-control form-control-lg form-control-a"
                      id="inputPassword"
                      placeholder="Password"
                      type="password"
                      v-model="password"
                      v-validate="{ required: true, min: 6 }"
                      name="password"
                    />
                  </div>
                </div>

                <div class="col-md-8 mb-3 block">
                  <div class="form-group">
                    <label for="inputName">Confirm Your Password</label>
                    <input
                      class="form-control form-control-lg form-control-a"
                      id="inputConfirmPassword"
                      placeholder="Confirm password"
                      type="password"
                      v-model="passwordConf"
                    />
                  </div>
                </div>

                <div class="col-md-8 mb-3 block">
                  <div class="float-left">
                    <ul class="list-unstyled">
                      <li class="item-list-a">
                        <router-link to="/login">
                          Already member?
                          <span style="color:blue">Log in</span> here
                        </router-link>
                      </li>
                    </ul>
                  </div>
                  <div class="float-right">
                    <div class="col-md-12">
                      <button type="submit" class="btn btn-a">Register</button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Footer from "@/components/Footer";

export default {
  data() {
    return {
      email: "",

      password: "",
      passwordConf: "",
      is_admin: null,
      errors: []
    };
  },
  components: {
    Footer
  },
  methods: {
    register: function() {
      this.errors = [];
      if (this.password != this.passwordConf) {
        this.errors.push("Password Need match");
      }
      if (!this.password) {
        this.errors.push("Password is needed");
      }
      if (!this.email) {
        this.errors.push("Email required.");
      } else if (!this.validEmail(this.email)) {
        this.errors.push("Valid email required.");
      }

      let data = {
        email: this.email,
        password: this.password,
        is_admin: this.is_admin
      };

      this.$store
        .dispatch("register", data)
        .then(() => this.$router.push("/login"))
        .catch(err => console.log(err));
    },
    validEmail: function(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }
  }
};
</script>

<style scoped>
</style>